import React from 'react';
import { Link } from "react-router-dom";
import memberPics from '../../img/team/**.jpg';
import girrafePic from '../../images/pic_9.png';
import toyPic from '../../images/pic_12.png';
import qrPic from '../../img/Alphabet_game.png'

export default function AboutUs () {
  return(
    <div className="container">
			<div className="s_width">
				<header>
        <div id="for_header">
          <h1>
            <span className="stroke"></span><br/>
            <span id="big-letter">A</span>
            <span id="second-word">
              <span className="stroke"></span>
            </span>
          </h1>
          <p className="title_primary">
            <span></span> <span></span> <span></span> <span></span>
          </p>
          </div>
          <div className="navigation">
            <Link to="/alphabet/learn">
              <p className="nav_link" id="link-1" href=""><span></span></p>
            </Link>
            <Link to="/alphabet/game">
              <p className="nav_link" id="link-2" href=""><span></span></p>
            </Link>
            <Link to="/alphabet">
              <p className="nav_link nav_about" id="link-3" href=""><span></span> <span></span></p>
            </Link>
				  </div>
				</header>
        <div id="about-description">
          <h2 id="about-title"><span></span> <span></span>:</h2>
          <p className="text">
                     , 
             "A-Level Poltava"  "Front-End".
                 : HTML5, CSS3, JavaScript, React. 
                  . 
                ,   . 
          </p>
        </div>
        <div id="members-container">
          {/* <h2 id="team-title"><span></span> <span></span>:</h2> */}
          <div className="team-member">
            <img src={memberPics['slavik']} alt=""/>
            <h3> </h3>
          </div>
          <div className="team-member">
            <img src={memberPics['andrew']} alt=""/>
            <h3> </h3>
          </div>
          <div className="team-member">
            <img src={memberPics['sveta']} alt=""/>
            <h3> </h3>
          </div>
          <div className="team-member">
            <img src={memberPics['alexandr']} alt=""/>
            <h3> </h3>
          </div>
        </div>
        <img src={girrafePic} alt="" id="girrafe-pic"/>
        <img src={toyPic} alt="" id="toy-pic"/>
        <img src={qrPic} alt="" id="qr-pic"/>
			</div>
		</div>
  )
}